ard__result-block .puzzle-piece {\n    animation-name: forward;\n}\n\n.game-board__source-block .puzzle-piece {\n    animation-name: backward;\n}\n\n@media (hover: hover) and (pointer: fine) {\n    .puzzle-piece-bg:hover::after,\n    .game-board__puzzle-line-child:hover .puzzle-piece-bg:after {\n        background: #e2e2e2;\n    }\n\n    .game-board__puzzle-line.completed\n        .game-board__puzzle-line-child:hover\n        .puzzle-piece-bg:after {\n        background: #ffffff;\n    }\n\n    .game-board__puzzle-line-child:hover .puzzle-piece {\n        background: #e2e2e2;\n    }\n    .game-board__puzzle-line.completed:hover .puzzle-piece {\n        cursor: auto;\n        background: #ffffff;\n    }\n}\n\n@keyframes forward {\n    0% {\n        top: 150px;\n        scale: 0;\n        opacity: 0.5;\n    }\n\n    100% {\n        top: 0px;\n        opacity: 1;\n        scale: 1;\n    }\n}\n\n@keyframes backward {\n    0% {\n        top: -150px;\n        scale: 0;\n        opacity: 0.5;\n    }\n\n    100% {\n        top: 0px;\n        opacity: 1;\n        scale: 1;\n    }\n}\n\n.game-board__check-btn,\n.game-board__autocomplete-btn {\n    min-width: 110px;\n    padding: 0 10px;\n    min-height: 40px;\n    background: rgb(227, 126, 25);\n}\n\n.game-board__autocomplete-btn {\n    background: rgb(170, 25, 227);\n\n    &:disabled {\n        background: #000000;\n        cursor: auto;\n    }\n}\n\n.game-board__check-btn[value='Continue'] {\n    background-color: #198115;\n}\n\n.game-board__check-btn:disabled {\n    background: #000;\n    cursor: auto;\n}\n\n.wrong-position:after {\n    width: 100%;\n    background: red;\n}\n\n.correct-position:after {\n    width: 100%;\n    background: green;\n}\n\n.round-descr {\n    padding: 5px 0;\n    font-family: 'Space Mono', monospace;\n    text-align: center;\n    font-size: 18px;\n    text-transform: uppercase;\n    background: rgba(255, 255, 255, 0.3);\n    -webkit-text-stroke: 0.5px black;\n    opacity: 0.8;\n}\n\n#level-1 {\n  