import appLoader from '../..';
import ControlPanel from '../control-panel/controlPanels';
import BaseElement from '../utils/BaseElement';
import InputElement from '../utils/InputElement';
import deleteUser from '../utils/deleteUser';
import './header.scss';

export default class Header {
    header: HTMLElement | null = null;

    constructor() {
        this.header = new BaseElement('header', undefined, [
            'header',
        ]).getElement();
    }

    private init() {
        const logOutBtn = new InputElement(
            'button',
            'Logout',
            undefined,
            undefined,
            ['button', 'logout-btn']
        ).getElement();

        logOutBtn.addEventListener('click', () => {
            ControlPanel.saveUserSettings('default');
            deleteUser();
            while (document.body.lastElementChild)
                document.body.lastElementChild.remove();
            appLoader.load();
        });
        if (this.header) {
            const headerWrapper = new BaseElement('div', undefined, [
                'wrapper',
                'header-wrapper',
            ]).getElement();
            headerWrapper.append(logOutBtn);
            this.header.append(headerWrapper);
        }
    }

    getElement() {
        this.init();
        return this.header;
    }
}
